:root{--primaryColor: #e03e0e;--primaryColor2: #fa8717;--primaryColor3: #fee6cf;--bannerColor: #ea5023;--fontFamily: "Nunito", sans-serif;--templateColor: #e03e0e;--templateColor2: #ff8a73;--whiteColor: #ffffff;--lightColor: #f8f8fc;--darkColor: #7c7c80;--blackColor: #000000;--paragraphColor: #818992;--transition: 0.5s;--darkColor1: #474747;--blue: #4a50ff;--blueActive: #7d81f4;--pink: #d74a96;--pinkHover: #f076b8;--lightBlue: #6195fd;--lightBlueHover: #1763fb;--mint: #24b6b1;--mintHover: #2c9491;--green: #47821c;--greenHover: #5bb01c;--purpule: #7b70ff;--purpleHover: #5145e2;--yellow: #ffc736;--yellowHover: #fbb911;--breadcrumbtextcolor: #d1c4e9;--breadcrumbtextcoloractive: #fff;--lightOrange: #ffc61a;--orange1: #f16c1c}.notification__page .notification-area{background:linear-gradient(180deg, #f68318 0%, #f1691b 100%);padding-top:160px;padding-left:12vw;padding-right:12vw;padding-bottom:90px;min-height:100vh}.notification__page .notification-area .notification-wrapper{background:#ffffff;border:5px solid #f2f2f2;border-radius:40px;padding:56px 10% 56px 10%;display:flex;flex-direction:column;align-items:flex-start;min-height:80vh}.notification__page .notification-area .notification-wrapper .notification-item-wrapper{cursor:pointer;position:relative;width:100%}.notification__page .notification-area .notification-wrapper .notification-item-wrapper:hover{opacity:0.6}.notification__page .notification-area .notification-wrapper .notification-item-wrapper .notification-divider{margin-top:36px;margin-bottom:36px;background-color:rgba(229,229,229,0.5);opacity:1;height:2px}.notification__page .notification-area .notification-wrapper .notification-item-wrapper .notification-date{font-style:normal;font-weight:500;font-size:14px;line-height:17px;color:#c4c4c4;margin-bottom:10px}.notification__page .notification-area .notification-wrapper .notification-item-wrapper .notification-title{font-style:normal;font-weight:bold;font-size:18px;line-height:22px;color:#1e3d74}.notification__page .notification-area .notification-wrapper .notification-item-wrapper .notification-subtitle{font-style:normal;font-weight:500;font-size:18px;line-height:22px;color:#c4c4c4;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical}.notification__page .notification-area .notification-wrapper .notification-item-wrapper .unseen-badge{position:absolute;width:20px;height:20px;background-color:#ffa300;border-radius:10px;right:0;top:50%;transform:translate(0, -50%)}.content-modal{min-width:40vw !important;min-height:40vh !important;background:#ffffff;border:5px solid #f2f2f2;box-shadow:0px 0px 100px rgba(0,0,0,0.1);border-radius:40px;padding:45px 50px 45px 50px;position:relative;margin-top:20vh}.content-modal .noti-close-btn{position:absolute;top:-60px;right:-60px;cursor:pointer}.content-modal .noti-close-btn:active{transform:scale(0.9);transition:transform 0.1s ease-in}.noti-content-wrapper{user-select:none}.noti-content-wrapper .noti-title{font-style:normal;font-weight:800;font-size:30px;line-height:36px;color:#1e3d74;margin-bottom:20px}.noti-content-wrapper .noti-image-wrapper{width:100%;aspect-ratio:1;border-radius:40px}.noti-content-wrapper .noti-image-wrapper .noti-image{width:100%;height:100%;object-fit:cover;border-radius:40px}.noti-content-wrapper .noti-content{font-weight:500;font-size:18px;line-height:22px;text-align:justify;color:#1e3d74}.modal-body{padding:0px}
